$bgblue: #1A2B50; //primary color
$blue: #84AFDD; //secondary color
$hblue: #3d78b8; //heading color
$green: #88ad8b; //third color

body {
  color: white;
}

h5.mt-5, .h5 {
  color: $blue;
}

span {
  color: white;
}

img.logo {
  justify-content: center;
}

button.btn.btn-link, button.mt-4.btn.btn-primary {
  margin-right: 20px !important;
  padding: 10px !important;
  margin-top: 20px !important;
}

button.fav-btn {
  font-weight: 600;
  padding: 15px;
  align-content: center;
}

button.fav-btn:hover {
  text-decoration: none;
  background-color: white;
  color: $bgblue;
}